@import "../common/mixins";
@import "../common/variable";

.welcome-card {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  color: #fff;
  background-color: $colorOrange;
  &__media {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    font-size: 0;
    picture {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      width: 100%;
      font-size: 0;
    }
    img {
      width: 100%;
      height: 100%;
      -o-object-fit: cover;
         object-fit: cover;
      aspect-ratio: 1.598 / 1;
    }
  }
  &__main {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    grid-gap: r(30);
    padding: r(25);
    padding-bottom: r(35);
    text-align: center;
    @include max-tablet {
      padding: r(35) r(15);
    }
  }
  &__title {
    font-size: r(38);
    font-weight: bold;
    color: currentColor;
    @include max-tablet {
      font-size: r(26);
    }
  }
  &__excerpt {}
  &__action {
    margin-top: auto;
    .button-global {
      &:hover {
        color: $textColor;
        background-color: $colorWhite;
      }
    }
  }
}